body {
  margin: 0;
  padding: 0;
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Courier",
    monospace;
}

.navbar .correct {
  -webkit-animation: .4s correct;
          animation: .4s correct;
}

.navbar .incorrect {
  -webkit-animation: .4s incorrect;
          animation: .4s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@-webkit-keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}

@keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}

.navbar {
  background: rgb(255, 166, 0);
  min-height: 70px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color: white;
  font-size: calc(10px + (24 - 10) * (100vw - 320px) / (1200 - 400));
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  display: inline-block;
  padding: 26px 0px;
  width: 33.3%;
  text-align: center;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-weight: 900;
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
  }

  .navbar li:nth-child(2) {
    width: 35%;
  }

  .navbar li:last-child {
    width: 40%;
  }
}

.header {
  background-color: #2C323C;
  background-image: url("/assets/images/tileable_wood_texture.png");
  background-repeat: repeat;
  color: white;
  min-height: 400px;
  padding: 180px 20px; 
  padding-bottom: 140px;
  text-align: center;
}

.header h1 {
  margin: 0;
  margin-bottom: 30px;
  font-size: 4rem;
}

.container {
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 90%;
  margin: 0 auto;
}

@media (min-width: 1100px) {
  .container {
    padding-left: 100px;
    padding-right: 100px;
    width: 1100px;
  }
}

.click-item {
  background-size: cover;
  background-position: center;
  height: 170px;
  width: 170px;
  margin: 8px 25px;
  border: 5px solid rgb(241, 241, 241);
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
}

.click-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: 0.1s;
}

.shake {
  -webkit-animation: shake .5s 0.0125s;
          animation: shake .5s 0.0125s;
}

@-webkit-keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
            transform: translate(10px);
  }
}

@keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  12.5%,
  37.5%,
  62.5%,
  87.5% {
    -webkit-transform: translate(-10px);
            transform: translate(-10px);
  }

  25%,
  50%,
  75% {
    -webkit-transform: translate(10px);
            transform: translate(10px);
  }
}

@media (max-width: 750px) {
  .click-item {
    margin: 10px;
  }
}

@media (max-width: 500px) {
  .click-item {
    margin: 10px;
  }
}

.footer {
  background: 
  rgb(255, 166, 0);
  height: 100px;
  position: relative;
  color: white;
  overflow: auto;
}

.footer > .bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background:rgb(255, 166, 0);
  padding: 8px;
  font-weight: 600;
}

.footer > .bottom > img {
  width: 20px;
  position: relative;
  top: 3px;
  left: 2px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

